p\">\n                <div class=\"d-flex justify-content-end\">\n                    <i\n                        @click=\"showForm = false\"\n                        style=\"font-size: 30px; cursor: pointer\"\n                        class=\"fas fa-times-circle\"\n                    ></i>\n                </div>\n                <div class=\"cont-around\">\n                    <div class=\"form-group\">\n                        <label for=\"date\">Date</label>\n                        <input\n                            type=\"date\"\n                            class=\"form-control\"\n                            id=\"date\"\n                            v-model=\"submitForm.date\"\n                        />\n                    </div>\n                    <div class=\"form-group\">\n                        <label for=\"priority\">Priority</label>\n                        <input\n                            type=\"number\"\n                            class=\"form-control\"\n                            id=\"priority\"\n                            v-model=\"submitForm.priority\"\n                        />\n                    </div>\n                </div>\n                <div class=\"cont-around\">\n                    <div class=\"form-group\">\n                        <label for=\"page_name\">Page Name/Path</label>\n                        <input\n                            type=\"text\"\n                            class=\"form-control\"\n                            id=\"page_name\"\n                            v-model=\"submitForm.page_name\"\n                        />\n                    </div>\n                    <div class=\"form-group\">\n                        <label for=\"web_url\">Web URL</label>\n                        <input\n                            type=\"text\"\n                            class=\"form-control\"\n                            id=\"web_url\"\n                            v-model=\"submitForm.web_url\"\n                        />\n                    </div>\n                </div>\n                <div class=\"form-group\">\n                    <label for=\"Challanges\">Challanges</label>\n                    <textarea\n                        class=\"form-control\"\n                        id=\"Challanges\"\n                        rows=\"3\"\n                        v-model=\"submitForm.challanges\"\n                    ></textarea>\n                </div>\n                <div class=\"form-group\">\n                    <label for=\"Recommendation\">Recommendations</label>\n                    <textarea\n                        class=\"form-control\"\n                        id=\"Recommendation\"\n                        rows=\"3\"\n                        v-model=\"submitForm.recommendation\"\n                    ></textarea>\n                </div>\n                <div class=\"cont-around multiple_cont\">\n                    <div class=\"form-group\">\n                        <label for=\"assigned_to\">Assigned To</label>\n                        <input\n                            type=\"text\"\n                            class=\"form-control\"\n                            id=\"assigned_to\"\n                            v-model=\"submitForm.assigned_to\"\n                        />\n                    </div>\n                    <div class=\"form-group\">\n                        <label for=\"reported_by\">Reported By</label>\n                        <input\n                            type=\"text\"\n                            class=\"form-control\"\n                            id=\"reported_by\"\n                            v-model=\"submitForm.reported_by\"\n                        />\n                    </div>\n                    <div class=\"form-group\">\n                        <label for=\"status\">Status</label>\n                        <select\n                            size=\"2\"\n                            class=\"form-control\"\n                            id=\"status\"\n                            v-model=\"submitForm.status\"\n                        >\n                            <option>Pending</option>\n                            <option>Done</option>\n                        </select>\n                    </div>\n                </div>\n                <div class=\"form-group\">\n                    <label for=\"Remarks\">Remarks</label>\n                    <textarea\n                        class=\"form-control\"\n                        id=\"Remarks\"\n                        rows=\"3\"\n                        v-model=\"submitForm.remarks\"\n                    ></textarea>\n                </div>\n                <button type=\"submit\" class=\"btn btn-primary mb-2 btn-custm\">\n                    Save\n                </button>\n            </form>\n        </div>\n        <div class=\"container position-relative\">\n            <div class=\"site-tasks-head\">\n                <button type=\"button\" @click=\"formShow()\">Add Tasks</button>\n                <p>List of Site Tasks</p>\n            </div>\n            <table class=\"table table-striped\">\n                <thead>\n                    <tr>\n                        <th scope=\"col\">#</th>\n                        <th scope=\"col\">Date</th>\n                        <th scope=\"col\">Priority</th>\n                        <th scope=\"col\">Page Name/Path</th>\n                        <th scope=\"col\">Web URL</th>\n                        <th scope=\"col\">Challanges</th>\n                        <th scope=\"col\">Recommendations</th>\n                        <th scope=\"col\">Assigned To</th>\n                        <th scope=\"col\">Reported By</th>\n                        <th scope=\"col\">Status</th>\n                        <th scope=\"col\">Remarks</th>\n                        <th scope=\"col\">Actions</th>\n                    </tr>\n                </thead>\n                <tbody>\n                    <tr v-for=\"(item, index) in allTasks\" :key=\"index\">\n                        <th scope=\"row\">{{ index }}</th>\n                        <td>{{ item.date }}</td>\n                        <td>{{ item.priority }}</td>\n                        <td>{{ item.page_name }}</td>\n                        <td>{{ item.web_url }}</td>\n                        <td>{{ item.challanges }}</td>\n                        <td>{{ item.recommendation }}</td>\n                        <td>{{ item.assigned_to }}</td>\n                        <td>{{ item.reported_by }}</td>\n                        <td>{{ item.status }}</td>\n                        <td>{{ item.remarks }}</td>\n                        <td class=\"edit-btn-tab\">\n                            <button\n                                @click=\"editForm($event, index)\"\n                                type=\"button\"\n                            >\n                                <i class=\"fas fa-edit\"></i></button\n                            ><button @click=\"deleteitem(item.id)\" type=\"button\">\n                                <i class=\"fas fa-trash-alt\"></i>\n                            </button>\n                        </td>\n                    </tr>\n                </tbody>\n            </table>\n        </div>\n        <flashMessage :message=\"message\"></flashMessage>\n    </div>\n</template>\n\n<script>\nimport flashMessage from \"../FlashMessage.vue\";\n\nexport default {\n    components: {\n        flashMessage,\n    },\n    data() {\n        return {\n            submitForm: {\n                date: null,\n                priority: null,\n                page_name: null,\n                web_url: null,\n                challanges: null,\n                recommendation: null,\n                assigned_to: null,\n                reported_by: null,\n                status: null,\n                remarks: null,\n            },\n            allTasks: [],\n            showForm: false,\n            updateForm: false,\n            updateId: null,\n            message: null,\n        };\n    },\n    methods: {\n        submit(e) {\n            let _this = this;\n            let formData = new FormData();\n            let data = JSON.stringify(_this.submitForm);\n            formData.append(\"data\", data);\n            console.log(formData, data);\n            let url = \"/api/sitetasks\";\n            if (_this.updateForm == true) {\n                url = `/api/sitetasks/${_this.updateId}`;\n                formData.append(\"_method\", \"PATCH\");\n            }\n\n            axios\n                .post(url, formData)\n                .then(function (response) {\n                    for (const key in _this.submitForm) {\n                        _this.submitForm[key] = null;\n                    }\n                    _this.showForm = false;\n                    if (_this.updateForm == true) {\n                        _this.message = \"UPDATED!!\";\n                    } else {\n                        _this.message = \"Submitted!!\";\n                    }\n                    setTimeout(function () {\n                        _this.message = null;\n                    }, 3000);\n                    _this.updateForm = false;\n                    _this.updateId = null;\n                    _this.getTasks();\n                    console.log(response);\n                })\n                .catch((error) => {\n                    _this.message = \"Something went wrong!!\";\n                    console.log(error);\n                });\n        },\n        getTasks() {\n            let _this = this;\n            axios\n                .get(\"/api/sitetasks\")\n                .then(function (response) {\n                    _this.allTasks = response.data;\n                    console.log(response);\n                })\n                .catch((error) => {\n                    console.log(error);\n                });\n        },\n        editForm(e, i) {\n            let _this = this;\n            _this.updateId = _this.allTasks[i].id;\n            _this.submitForm = _this.allTasks[i];\n            delete _this.submitForm.id;\n            delete _this.submitForm.updated_at;\n            delete _this.submitForm.created_at;\n            delete _this.submitForm.slug;\n            _this.updateForm = true;\n            _this.showForm = true;\n        },\n        formShow() {\n            let _this = this;\n            _this.submitForm = {\n                date: null,\n                priority: null,\n                page_name: null,\n                web_url: null,\n                challanges: null,\n                recommendation: null,\n                assigned_to: null,\n                status: null,\n                remarks: null,\n            };\n            _this.showForm = true;\n        },\n        deleteitem(i) {\n            if (confirm(\"Are you sure you want to delete?\")) {\n                let _this = this;\n                axios\n                    .delete(`api/sitetasks/${i}`)\n                    .then(function (response) {\n                        _this.message = \"Deleted\";\n                        _this.getTasks();\n                        setTimeout(function () {\n                            _this.message = null;\n                        }, 3000);\n                    })\n                    .catch((error) => {\n                        console.log(error);\n                        _this.message = \"Spmething went wrong!!\";\n                    });\n            } else {\n                // Do nothing!\n                return;\n            }\n        },\n    },\n    mounted() {\n        this.$nextTick(function () {\n            //console.log(this.vendorCat, this.vendor);\n            this.getTasks();\n        });\n    },\n};\n</script>\n\n<style scoped>\n.task-form-emp {\n    width: 60%;\n    background-color: #fafafa;\n    margin: 4em auto;\n    padding: 3em;\n    border-radius: 15px;\n    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;\n}\n.task-form-emp label {\n    font-family: \"Poppins\";\n    font-style: normal;\n    font-weight: 600;\n    font-size: 17px;\n    line-height: 49px;\n    color: #000000;\n}\n.cont-around {\n    display: flex;\n    justify-content: space-around;\n}\n.cont-around > div {\n    width: 45%;\n}\n.multiple_cont > div {\n    width: 32%;\n}\n.btn-custm {\n    margin-top: 2em;\n}\n.edit-btn-tab > button {\n    background-color: #461952;\n    color: white;\n    outline: none;\n    border: none;\n    border-radius: 50%;\n    width: 30px;\n    height: 30px;\n    padding: 1px 0px 0px 1px;\n    margin-bottom: 10px;\n}\n.site-tasks-head > button {\n    background-color: #461952;\n    color: white;\n    outline: none;\n    border: none;\n    padding: 5px 15px;\n    border-radius: 10px;\n}\n.site-tasks-head > p {\n    font-family: \"Lato\", sans-serif;\n    font-style: normal;\n    font-weight: 700;\n    font-size: 36px;\n    line-height: 43px;\n    text-align: center;\n    color: #461952;\n    width: 100%;\n}\n.overlay-form {\n    position: fixed;\n    top: 0;\n    width: 100%;\n    height: 100%;\n    overflow-y: auto;\n    background-color: rgba(0, 0, 0, 0.4);\n    z-index: 999;\n}\n</style>\n"],"sourceRoot":""}]);
